// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import MuseUI from 'muse-ui'
import 'muse-ui/dist/muse-ui.css'
import './assets/iconfont/iconfont.css'// 图形字体库
import store from './store/index.js'// 全局挂载状态管理
import loading from './components/loading'

import VueRouter from 'vue-router'// 导入路由

import 'animate.css' // 导入动画组件
import './assets/css/reset.css'// 导入全局reset

import VueResource from 'vue-resource'// 导入数据交互模块
import Vuex from 'vuex'

Vue.use(VueRouter)
Vue.use(VueResource)
Vue.use(MuseUI)
Vue.use(loading)
Vue.use(Vuex)

Vue.http.interceptors.push((request, next) => {
  store.dispatch('showLoading')
  next((response) => {
    setTimeout(function () {
      store.dispatch('hidLoading')
    }, 100)
    return response
  })
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
  store
})



