
import Vue from 'vue'
import Router from 'vue-router'

import Home from '../components/home.vue'
import Search from '../components/search.vue'
import Raking from '../components/raking.vue'
import TopList from '../components/toplist.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/search',
      name: 'Search',
      component: Search
    },
    {
      path: '/raking',
      name: 'Raking',
      component: Raking
    },
    {
      path: '/toplist',
      name: 'Toplist',
      component: TopList
    }
  ]
})



